export class StoreServiceInput {
    MerchGroupIDs: string;
    FromDate: Date;
    ToDate: Date;

    constructor(merchGroupIDs: string, fromDate: Date, toDate: Date) {
        this.MerchGroupIDs = merchGroupIDs;
        this.FromDate = fromDate;
        this.ToDate = toDate;
    }
}

export class StoreService {
    Branch: string;
    Merchandiser: string;
    Date: string;
    Chain: string;
    StoreName: string;
    StartTime: string;
    EndTime: string;
    ManagerName: string;
    ManagerSignature: string;
    CasesWorked: number;
    CasesInBackStop: number;
    StorePics: string;
    PicsLocation: string;
}

export class UserMerchGroupInput {
    UserGSN: string;

    constructor(usrGSN: string) {
        this.UserGSN = usrGSN;
    }
}

export class UserMerchGroup {
    id: number;
    name: string;
}

export class ImageInput {
    BlobID: string;
    IsSignature: number;

    constructor(blobid: string, isSign: number) {
        this.BlobID = blobid;
        this.IsSignature = isSign;
    }
}

export class ImageDetail {
    RelativeURL: string;
    AbsoluteURL: string;
    Container: string;
    StorageAccount: string;
    AccessLevel: string;
    ConnectionString: string;
}

export class ImageURLInput {
    RelativeURL: string;
    AbsoluteURL: string;
    Container: string;
    StorageAccount: string;
    AccessLevel: string;
    AzureConnectionString: string;

    constructor(relativeURL: string, absoluteURL: string, container: string, storageaccount: string, accesslevel: string, connectionstring: string) {
        this.RelativeURL = relativeURL;
        this.AbsoluteURL = absoluteURL;
        this.Container = container;
        this.StorageAccount = storageaccount;
        this.AccessLevel = accesslevel;
        this.AzureConnectionString = connectionstring;
    }
}
